@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}.registro__container{background-color:transparent;border:none;padding:0px;width:100%}.footer{align-items:flex-start;flex-direction:column}}body{margin:0px;background-color:#21C08B;font-family:"Muli",sans-serif}*{box-sizing:border-box}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}.carousel-item{background-color:transparent;width:200px;height:250px;border-radius:20px;overflow:hidden;margin-right:10px;display:inline-block;cursor:pointer;transition:450ms all;transform-origin:center left;position:relative;font-size:20px}.carousel-item:hover ~ .carousel-item{transform:translate3d(100px, 0, 0)}.carousel-item__img{width:200px;height:250px;-o-object-fit:cover;object-fit:cover}.carousel-item__details{align-items:flex-start;display:flex;flex-direction:column;justify-content:flex-end;background:linear-gradient(to top, rgba(0,0,0,0.9), rgba(0,0,0,0), 100%);font-size:10px;opacity:0;transition:450ms opacity;padding:10px;position:absolute;top:0;left:0;right:0;bottom:0}.carousel-item__details:hover{opacity:1}.carousel-item__details--img{width:20px}.carousel-item__details--title{color:white;margin:5px 0px 0px 0px}.carousel-item__details--subtitle{color:white;font-size:8px;margin:3px 0px}
