@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}.registro__container{background-color:transparent;border:none;padding:0px;width:100%}.footer{align-items:flex-start;flex-direction:column}}body{margin:0px;background-color:#21C08B;font-family:"Muli",sans-serif}*{box-sizing:border-box}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}.login{background:linear-gradient(#21C08B, #5630fb);display:flex;align-items:center;justify-content:center;flex-direction:column;padding:0px 30px;min-height:calc(100vh - 200px)}.login__container{background-color:rgba(255,255,255,0.1);border:2px solid white;border-radius:40px;box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22);color:white;display:flex;flex-direction:column;justify-content:space-around;padding:60px 68px 40px;width:450px}.login__container--form{display:flex;flex-direction:column}.login__container--form label{font-size:14px}.login__container--remember-me{color:white;display:flex;justify-content:space-between;margin-top:10px}.login__container--remember-me a{color:white;font-size:14px;text-decoration:none}.login__container--remember-me a:hover{text-decoration:underline}.login__container_social-media>div{display:flex;align-items:center;font-size:14px;margin-bottom:10px}.login__container_social-media>div>img{width:30px;margin-right:10px}.login__container--register{font-size:14px}.login__container--register a{font-size:16px;color:white;font-weight:bold;text-decoration:none}.login__container--register a:hover{text-decoration:underline}
