@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}.registro__container{background-color:transparent;border:none;padding:0px;width:100%}.footer{align-items:flex-start;flex-direction:column}}body{margin:0px;background-color:#21C08B;font-family:"Muli",sans-serif}*{box-sizing:border-box}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}.carousel{width:100%;padding:30px;position:relative}.carousel__container{white-space:nowrap;margin:70px 0px;padding-bottom:10px;font-size:0;transition:450ms transform}.carousel__container:hover .carousel-item{opacity:0.3}.carousel__container:hover .carousel-item:hover{transform:scale(1.5);opacity:1}
