@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}.registro__container{background-color:transparent;border:none;padding:0px;width:100%}.footer{align-items:flex-start;flex-direction:column}}body{margin:0px;background-color:#21C08B;font-family:"Muli",sans-serif}*{box-sizing:border-box}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}video{-o-object-fit:cover;object-fit:cover;width:100vw;height:100vh;z-index:0;position:absolute;top:0;left:0}video:hover .Player-back{display:block}.Player-back{position:fixed;top:5px;left:5px;font-size:12px}.Player-back button{text-decoration:none;color:white;background:rgba(0,0,0,0.3);padding:5px;border-radius:5px;border:0;outline:0;cursor:pointer}
