@media only screen and (max-width: 600px){.login__container{background-color:transparent;border:none;padding:0px;width:100%}.registro__container{background-color:transparent;border:none;padding:0px;width:100%}.footer{align-items:flex-start;flex-direction:column}}body{margin:0px;background-color:#21C08B;font-family:"Muli",sans-serif}*{box-sizing:border-box}.button{background-color:rgba(255,255,255,0.3);border:none;border-radius:25px;color:white;cursor:pointer;font-size:16px;font-weight:bold;font-family:'Muli', sans-serif;height:50px;letter-spacing:1px;margin:10px 0px}.header{background-color:#21C08B;width:100%;height:80px;display:flex;justify-content:space-between;align-items:center}.header__img{width:200px;margin-top:10px;margin-left:10px}.header__menu{margin-right:30px}.header__menu ul{display:none;list-style:none;padding:0px;position:absolute;width:120px;text-align:right;margin:0px 0px 0px -14px}.header__menu:hover ul,ul:hover{display:block}.header__menu li{margin:10px 10px}.header__menu li a{color:white;text-decoration:none}.header__menu li a:hover{text-decoration:underline}.header__menu--profile{margin-right:8px;display:flex;align-items:center}.header__menu--profile img{margin-right:8px;width:40px;border-radius:100%;-o-object-fit:cover;object-fit:cover}.header__menu--profile p{margin:0px;color:white}
